#main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

span {
  font-size: 2rem;
  font-weight: 600;
}

button {
  display: block;
  padding: 5px 10px;
  width: 200px;
  margin: 10px 0;
  background-color: black;
  color: white;
  text-transform: uppercase;
  letter-spacing: 2px;
  border-radius: 6px;
  font-size: 0.7rem;
}